// -------------- THEME SWITCHER -------------- //
@mixin dark-appearance {
    filter: invert(1);

    img {
        filter: invert(1);

        &.ioda {
            filter: invert(0);
        }
    }
}

body[a="dark"] {
    @include dark-appearance;
}


@media (prefers-color-scheme: dark) {
    body[a="auto"] {
        @include dark-appearance;
    }
}

@import url('https://fonts.googleapis.com/css2?family=Courier+Prime&family=Roboto+Mono&display=swap');

html,
body {
    color: black;
    background: #f7f7f2;
    font-style: normal;
}

body a {
    text-decoration: none;
    color: #005772;
}

.hglt a {
    background-color: #afd8d8;
    color: #011835;
    border-radius: 5px;
    padding: 3px 7px;
}

body li {
    margin: 10px;
}

html {
    height: 100%;
}

body {
    font-family: "Courier Prime", monospace;
    font-size: 90%;
    line-height: 1.4;
    margin: 0;
    min-height: 100%;
    overflow-wrap: break-word;
    text-align: justify;
}

.webring {
    display: flex;
    justify-content: space-between;
    width: 50%;
}

h1 {
    // text-align: center;
    display: flex;
    font-size: 2em;
}

h2,
h3,
h4,
h5,
h6 {
    margin-top: 1.2em;
}

hr {
    margin: 2rem 0;

}

p {
    margin: 10px 0;
}

li {
    margin: 0.4rem 0;
}

*:target {
    background: yellow;
}

.w {
    max-width: 800px;
    margin: 0 auto;
    padding: 50px;
}

hr {
    text-align: center;
    border: 0;

    &:before {
        content: "--- END";
    }

    &:after {
        content: attr(data-content) " ---";
    }
}

table {
    width: 100%;
}

table,
th,
td {
    border: thin solid black;
    border-collapse: collapse;
    padding: 0.4rem;
}

pre {
    margin: 10px;
    padding: 10px 15px;
    border-radius: 5px;
    border: 2px solid black;
    overflow-x: auto;
}

code {
    font-family: 'Roboto Mono', monospace;
    font-size: 90%;
}

code.has-jax {
    -webkit-font-smoothing: antialiased;
    background: inherit !important;
    border: none !important;
    font-size: 100%;
}

blockquote {
    border: solid #444;
    padding: 2rem;
    margin: 10px 0;
    border-radius: 5px;

    p {
        margin: 0;
    }
}

img {
    max-width: 100%;
    align-content: center;
    border-radius: 5px;
    margin: 0 auto;
}

figcaption {
    text-align: center;
    opacity: 0.5;
}

.dsclmr {
    padding: 10px;
    color: #FCF8F0;
    background: #c35d5d;
    border-radius: 5px;
    text-align: center;
}

.house {
    font-size: 2em;
    display: flex;
}
